import React, { useEffect } from 'react';
import AOS from 'aos';

function BatchLevel() {
    useEffect(() =>{
        AOS.init({duration: 2000,
            delay: 200,
            once: false,
            mirror: false,
        });
        // AOS.init();
    }, []);
    return (
        <React.Fragment>
            <section className="my-5"></section>
            <div className="container">
            <div className="text-center text-white">
                <div className="row">
                    <div className="col-lg-4 col-12 bg-info" data-aos="fade-right" data-aos-delay="100">
                        <h3>Passed Batch</h3>
                        <h5>32</h5>
                    </div>
                    <div className="col-lg-4 col-12 bg-primary" data-aos="fade-down" data-aos-delay="1500">
                        <h3>Current Batch</h3>
                        <h5>6</h5>
                    </div>
                    <div className="col-lg-4 col-12 bg-dark" data-aos="fade-left" data-aos-delay="100">
                        <h3>Upcoming Batch</h3>
                        <h5>10</h5>
                    </div>
                </div>
            </div>
            </div>
        </React.Fragment>
    )
}

export default BatchLevel
