import React, { useEffect } from 'react';
import AOS from 'aos';
import styled from "styled-components";
import ReactModal from '../ReactModal';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom';

const Course = (props) =>{

    useEffect(() =>{
        AOS.init({duration: 2000,
            delay: 200,
            once: false,
            mirror: false,
        });
    }, []);

    return (
        <React.Fragment>
            <Card className="col-lg-6 col-sm-12">
                <div className="card" data-aos="zoom-in" data-aos-duration="2000">
                    <div className="card-header">
                        <h4> {props.title} </h4>
                    </div>
                    <div className="card-body">
                        <h5> {props.cOutLine} </h5>
                        <p> {props.pOne} </p>
                        <p> {props.pTwo} </p>
                        <p> {props.pThree} </p>
                        <p> {props.pFour} </p>
                        <p> {props.pFive} </p>
                        <p> {props.pSix} </p>
                        <p> {props.pSeven} </p>
                        <p> {props.pEight} </p>
                        <p> {props.pNine} </p>
                        <p> {props.pTen} </p>
                    </div>
                    <div className="card-footer text-center bg-dark mb-2">
                        <Button variant="contained" color="secondary">Resigtration Course</Button>
                    </div>
                    
                    <div className="">
                    <ReactModal></ReactModal>
                        <Link smooth className="btn btn-info float-right" to="/enrollCourse">Foreigner</Link>
                    </div>
                </div>
            </Card>
        </React.Fragment>
    )
}

export default Course

const Card = styled.div`
    // position: relative;
    // height: auto;
    // background: linear-gradient(-45deg, #000, #feae3f);
    // border-radius: 15px;
    // margin: 0 auto;
    // padding: 40px 20px;
    // box-shadow: 0 10px 15px rgba(0, 0, 0,);
    // transition: .5s;
    .card{
        height: 620px;
        padding: 15px;
        background: #EEEEEE;
        margin-bottom: 20px;
        h4{
            font-size: 20px;
        }
    }
    .card-header{
        background: #6C7A89;
        color: #000;
    }
`