import styled from "styled-components";

export const PAllCard = styled.div`
    position: relative;
    max-width: 300px;
    height: auto;
    background: linear-gradient(-45deg, #fe0847, #feae3f);
    border-radius: 15px;
    margin: 0 auto;
    padding: 40px 20px;
    box-shadow: 0 10px 15px rgba(0, 0, 0,);
    transition: .5s;
    
    &:hover{
        transform: scale(1.1);
    }
    .option ul{
        margin: 0;
        padding: 0; 
    }
    .option ul li{
        margin: 0 0 10px;
        padding: 0;
        list-style: none;
        color: #fff;
        font-size: 16px;
    }
    .col-sm-4:nth-child(1) &,
    .col-sm-4:nth-child(1) & .title .fa{
        background: linear-gradient(-45deg, #f403d1, #64b5f6);
    }
    .col-sm-4:nth-child(2) &,
    .col-sm-4:nth-child(2) & .title .fa{
        background: linear-gradient(-45deg, #ffec61, #f321d7);
    } 
    .col-sm-4:nth-child(3) &,
    .col-sm-4:nth-child(3) & .title .fa{
        background: linear-gradient(-45deg, #24ff72, #9a4eff);
    }
    .title .fa{
        color: #fff;
        font-size: 50px;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        text-align: center;
        line-height: 100px;
        box-shadow: 0 10px 10px rgb(0, 0, 0, .1);
    }
    .title h2{
        position: relative;
        margin: 20px 0 0;
        padding: 0;
        color: #fff;
        font-size: 25px;
        z-index: 2;
    }
    &::before{
        content: '';
        position: absolute;
        bottom: 70px;
        left: 55px;
        width: 68%;
        height: 40%;
        background: rgba(255, 255, 255, .1);
        z-index: 1;
        transform: skewY(-50deg) scale(1.5);
        }
    & a{
        position: relative;
        background: #fff;
        color: #000;
        width: 150px;
        height: 40px;
        line-height: 40px;
        border-radius: 40px;
        display: block;
        text-align: center;
        margin: 20px auto 0;
        font-size: 16px;
        cursor: pointer;
        text-decoration: none;
        box-shadow: 0 5px 10px rgba(0, 0, 0,) .1;
    }
    .price, .option{
        position: relative;
    }
    .price h4{
        margin: 0;
        padding: 20px 0;
        color: #fff;
        font-size:  50px;
    }
`;